.card {
  box-shadow: 0 10px 40px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  border-radius: 10px;
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  background-color: rgb(255 236 212);
  padding: 2rem;
}

.card:not(:first-child){
  margin-left: -10rem;
}

.card:last-child{
}

.card:hover{
  transform: translateX(1rem);
}

.cardDiv{
  display: flex;
  position: absolute;
}

